

























import Vue from 'vue';
import { mapGetters } from 'vuex';

import store from '@/store';

interface HTMLInputEvent extends Event {
    target: HTMLInputElement & EventTarget;
}

export default Vue.extend({
  name: 'Upload',
  computed: {
    ...mapGetters('step', [
      'step',
    ]),
    ...mapGetters('photo', [
      'base64',
    ]),
  },
  methods: {
    setBase64(e: HTMLInputEvent) {
      const file: File = (e.target.files as FileList)[0];
      const reader = new FileReader();
      reader.onload = (e: Event) => {
        store.dispatch('photo/setBase64', reader.result);
      };
      reader.readAsDataURL(file);
    },
    handleGetPhoto() {
      (this.$refs.inputFile as HTMLInputElement).click();
    },
    handleUploadPhoto() {
      store.dispatch('step/nextStep');
    },
  },
  mounted(): void {
    // const overlay: HTMLDivElement = (this.$refs.overlay as HTMLDivElement);
    //
    // const screenWidth = window.screen.width;
    // overlay.style.background =
    // `radial-gradient(ellipse calc(${screenWidth}px / 1.5
    // calc(${screenWidth}px) at center calc(50vh - 50px),
    // transparent 50%, rgba(0, 0, 0, 0.6) 50%)`;
  },
  destroyed(): void {
    store.dispatch('photo/deleteBase64');
  },
});
