import Vue from 'vue';
import { Photo } from '@/store/interfaces';

const state = {};
const mutations = {
  ADD_BASE64: (state: Photo, base64: string) => {
    Vue.set(state, 'base64', base64);
  },
  DELETE_BASE64: (state: Photo) => {
    Vue.delete(state, 'base64');
  },
};
const actions = {
  takeSnapshot: ({ commit }: {commit: Function}, video: HTMLVideoElement): string => {
    const canvas: HTMLCanvasElement = document.createElement('canvas');
    const width: number = video.offsetWidth;
    const height: number = video.offsetHeight;
    const context: CanvasRenderingContext2D|null = canvas.getContext('2d');

    canvas.width = width;
    canvas.height = height;
    if (context) context.drawImage(video, 0, 0, width, height);
    const imageBase64 = canvas.toDataURL('image/png');
    commit('ADD_BASE64', imageBase64);
    return imageBase64;
  },
  setBase64({ commit }: {commit: Function}, imageBase64: string) {
    commit('ADD_BASE64', imageBase64);
  },
  deleteBase64({ commit }: {commit: Function}) {
    commit('DELETE_BASE64');
  },
};
const getters = {
  base64: (state: Photo) => state.base64,
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
