import { RootState, Step } from '@/store/interfaces';

const state: Step = {
  step: 1,
  page: 'Description',
};
const mutations = {
  SET_STEP: (state: Step, step: { step: number, page: string }) => {
    state.step = step.step;
    state.page = step.page;
  },
};
const actions = {
  nextStep: ({ state, commit, rootState }: { state: Step,
    commit: Function, rootState: RootState }) => {
    switch (state.page) {
      case 'Description':
        commit('SET_STEP', {
          step: state.step,
          page: rootState.stream.isStreaming ? 'Photo' : 'Upload',
        });
        break;
      case 'Photo':
        commit('SET_STEP', {
          step: state.step,
          page: 'Accept',
        });
        break;
      case 'Upload':
      case 'Accept':
        if (state.step < 3) {
          commit('SET_STEP', {
            step: state.step + 1,
            page: 'Description',
          });
          break;
        } else {
          commit('SET_STEP', {
            step: state.step + 1,
            page: 'Success',
          });
          break;
        }
      default: break;
    }
  },
  prevStep: ({ state, commit }: { state: Step, commit: Function }) => {
    commit('SET_STEP', {
      step: state.step,
      page: 'Photo',
    });
  },
  failedStep: ({ state, commit }: { state: Step, commit: Function }) => {
    commit('SET_STEP', {
      step: state.step,
      page: 'Failed',
    });
  },
};
const getters = {
  step: (state: Step) => state.step,
  page: (state: Step) => state.page,
  stepDescription: (state: Step) => {
    switch (state.step) {
      case 1: {
        return 'Тримайте телефон вертикально та зробіть селфі';
      }
      case 2: {
        return 'Поверніть обличчя трохи праворуч та зробіть фото';
      }
      case 3: {
        return 'Поверніть обличчя трохи ліворуч та зробіть фото';
      }
      default: {
        return '';
      }
    }
  },
  stepHint: (state: Step) => {
    switch (state.step) {
      case 1: {
        return 'Розташуйте обличчя у контурі та зробіть селфі';
      }
      case 2: {
        return 'Розташуйте обличчя у контурі, повернувши трохи праворуч, та зробіть селфі';
      }
      case 3: {
        return 'Розташуйте обличчя у контурі, повернувши трохи ліворуч, та зробіть селфі';
      }
      default: {
        return '';
      }
    }
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
